<script context="module" lang="ts">

    import type Decimal from "decimal.js";

    import { d2n, n2d, pm, rand, randFrom } from "../core/Helpers";

    interface Self {
        
    }

    export interface ISequence {
        readonly abc: Decimal[];
        readonly answers: Decimal[];
        readonly equ?: string;
    }

    function variatePm(ans_: number | Decimal): Decimal[] {
        let ans = n2d(ans_);
        return [
            ans, 
            ans.plus(pm(1)),
            ans.plus(pm(2)),
            ans.plus(pm(3))
        ];
    }

    function applyDifs(a: number, difs: number[]): Decimal[] {
        let a2 = a + difs[0];
        let a3 = a2 + difs[1];
        let a4 = a3 + difs[2];
        let a5 = a4 + difs[3];
        let a6 = a5 + difs[4];

        return n2d([a, a2, a3, a4, a5, a6]);
    }

    export function newSequence(): ISequence {
        let sequences: (() => ISequence)[] = [

            // 0
            function arithmeticProgression() {
                let a = rand(1, 40);
                let d = pm(rand(2, 7));

                let difs = [d, d*2, d*3, d*4];

                let abc = applyDifs(a, difs);
                let ans = abc.pop();

                let answers = variatePm(ans);

                return {abc, answers}
            },

            // 1
            function geometricProgression() {

                let pows = [
                    n2d(0.1),
                    n2d(1).div(3),
                    n2d(0.5),
                    n2d(1),
                    n2d(1.5),
                    n2d(2),
                    n2d(3)
                ];

                let a = n2d(rand(1, 13));
                let p = randFrom(pows);
                let a2 = a.mul(p);
                let a3 = a2.mul(p);
                let ans = a3.mul(p);

                let abc = n2d([a, a2, a3]);

                let answers = n2d([
                    ans, 
                    ans.minus(p), 
                    ans.plus(p), 
                    ans.plus(2)
                ]);

                return {abc, answers}
            },

            // 2
            function _9_8_5_4_2() {
                let a = rand(1, 40);
                let d1 = pm(1);
                let d2a = pm(randFrom([2, 3, 4]));
                let d2d = pm(1);

                let difs = [d1, d2a, d1, d2a+d2d, d1];

                let abc = applyDifs(a, difs);
                let ans = abc.pop();

                let answers = variatePm(ans);

                return {abc, answers};
            },

            // 3
            function _3_5_4_6_5() {
                let a = rand(1, 40);
                let d1 = pm(2);
                let d2 = pm(1);

                let difs = [d1, d2, d1, d2, d1];

                let abc = applyDifs(a, difs);
                let ans = abc.pop();

                let answers = variatePm(ans);

                return {abc, answers};
            },

            // 4
            function _2_3_5_8_12() {
                let a = rand(1, 40);
                let da = pm(randFrom([1, 2]));
                let dd = pm(randFrom([1, 2]));

                let difs = [da, da+dd, da+2*dd, da+3*dd, da+4*dd];

                let abc = applyDifs(a, difs);
                let ans = abc.pop();

                let answers = variatePm(ans);

                return {abc, answers};
            },

            // 5
            function _1_3_2_4_3() {
                let a = rand(1, 40);

                do {
                    let d1 = pm(randFrom([1, 2, 3, 4]));
                    let d2 = pm(randFrom([1, 2, 3, 4]));

                    if (d1 === d2) continue;
                    if (d1 === -d2) continue;

                    let difs = [d1, d2, d1, d2, d1];

                    let abc = applyDifs(a, difs);
                    let ans = abc.pop();

                    let answers = variatePm(ans);

                    return {abc, answers};
                } 
                while (false);

            },

            // 6
            function _fibo() {
                let abc = n2d([1, 2, 3, 5, 8, 13]);
                let ans = 34;

                let answers = variatePm(ans);

                return {abc, answers};
            },
        ];

        return sequences[6]();

        return randFrom(sequences)();
    }

</script>