<script lang="ts">
    import { pulse } from '../core/Pulse.svelte';
    import { bounceOnDrag } from "../core/OnDrag.svelte"

    import type { WProblemStore } from './WProblem.Store'
    import { isNumeric } from '../core/Helpers';

    export let ga: string;
    export let store: WProblemStore["answers"][0];

    let {value, isClicked, isRight} = store;
    let bgColor: string;
    let isNumber: boolean;

    $: isNumber = isNumeric($value);

    $: bgColor = !$isClicked ? "bg-gray-100" :
        $isRight ? "bg-green-200" : "bg-red-200";
</script>


<button
    class="{bgColor}"
    style="grid-area:{ga};"
    use:pulse
    use:bounceOnDrag
    on:click
>
    {#if isNumber}
        {$value}
    {:else}
        <bdi dir="rtl"
            style="font-size:15px;" 
        >
            {$value}
        </bdi>
    {/if}
</button>
