import JSON5 from "json5"

import { splitNoEmpty } from "./Helpers";
import { error } from "./Logger.svelte";


interface Card {
    userId: string;
    content: string;
    config: string;
    problemId: string;
    answers: string;
    variables: string;
    sets: string;
    options: string;
}

declare let ankiCard: Card;

interface IAnki {
    hideLoader(): void;
    getCard(): Card;
    getConfig<Config>(): Config;
    commands(): Iterable<string[]>;
    tokens(): string[];
}

class CAnki implements IAnki {
    hideLoader() {
        let loader = document.getElementById("loader");
        loader.style.display = "none";
    }

    getCard(): Card {
        return ankiCard;
    }

    getConfig<Config>(): Config {
        try {
            return JSON5.parse(ankiCard.config);
        }
        catch (err) {
            error(err);
            return <any> {};
        }
    }

    *commands() {
        let content = ankiCard.content.replaceAll('\n', ' ');

        if (!content.includes(';')) return;

        let cmds = ankiCard.content.split(';')[0];

        for (let cmd of splitNoEmpty(cmds, ',')) {
            yield splitNoEmpty(cmd, ' ');
        }

    }

    tokens(): string[] {
        let content = ankiCard.content.replaceAll('\n', ' ');

        let tokens = content.includes(';') ?
            content.split(';')[1] :
            content;

        return splitNoEmpty(tokens, ',');
    }
}

export const Anki: IAnki = new CAnki();
