import { assert } from "./Asserts.svelte";
import { info } from "./Logger.svelte";

type OneOrMany<T> = T | T[];
type AsyncOrNot<T> = T | Promise<T>;

type Nope = AsyncOrNot<void>;
type NameOrNope<Name> = AsyncOrNot<keyof(Name) | undefined | void>;

type Config<State, Event, UiEvent> = {
    [StateName in keyof State]?: {
        map?: {
            [UiName in keyof UiEvent]?: keyof(Event) | 
                OneOrMany<(arg?: UiEvent[UiName]) => NameOrNope<Event>>
        },
        transitions?: {
            [EventName in keyof Event]?: keyof(State) |
                OneOrMany<(arg?: Event[EventName]) => NameOrNope<State>>
        },
        onEnter?: OneOrMany<(arg?: State[StateName]) => Nope>,
        onLeave?: OneOrMany<() => Nope>
    }
}

interface IFsm<State, Event, UiEvent> {
    enter<Name extends keyof(State)>(state: Name, arg?: State[Name]): Nope;
    fire<Name extends keyof(Event)>(name: Name, arg?: Event[Name]): Nope;
    fire<Name extends keyof(UiEvent)>(name: Name, arg?: UiEvent[Name]): Nope;
}

export function newFsm<State, Event, UiEvent>(config: Config<State, Event, UiEvent>)
    : IFsm<State, Event, UiEvent> {

    return <any> new Fsm(<any> config);
}


type Str = string | undefined | void;
type StrOrNope = AsyncOrNot<Str>;
type MappedHandler = (arg: any) => StrOrNope;
type TransitionHandler = (arg: any) => StrOrNope;
type EnterHandler = (arg: any) => Nope;
type LeaveHandler = () => Nope;

type AConfig = {
    [state: string]: {
        map?: {
            [name: string]: string | OneOrMany<MappedHandler>;
        },
        transitions?: {
            [name: string]: string | OneOrMany<TransitionHandler>;
        },
        onEnter?: OneOrMany<EnterHandler>,
        onLeave?: OneOrMany<LeaveHandler>
    }
}

interface AFsm {
    enter(state: string, arg?: any): Nope;
    fire(name: string, arg?: any): Nope;
}

interface Self {
    config: AConfig;
    state: string;
}

class Fsm implements AFsm {

    private readonly _my: Self;

    constructor(config: AConfig) {
        this._my = {
            config,
            state: undefined
        }
    }

    async enter(state: string, arg?: any) {
        let {_my} = this;

        if (_my.state === state) return;

        if (_my.state)
            await _impl.callLeaveHandlers(_my, _my.state);

        _my.state = state;
        console.log(`current state is '${state}'`)

        await _impl.callEnterHandlers(_my, state, arg);
    }

    async fire(name: string, arg?: any) {
        let {_my} = this;

        let { 
            map = [], 
            transitions = [] 
        } = _my.config[_my.state];

        let mapped = map[name];

        if (mapped && typeof mapped !== 'string') {
            mapped = await _impl.callMappedHandlers(_my.state, mapped, arg);

            if (!mapped) return;
        }

        let transition = transitions[mapped ?? name];

        if (!transition) {
            info(`Don't know how to fire '${name}' in '${_my.state}'`);
            return;
        }

        if (typeof transition === 'string') {
            await this.enter(transition, arg);
            return;
        }

        let toState = await _impl.callTransitionHandlers(_my.state, name, transition, arg);

        if (toState) {
            await this.enter(toState, arg);
        }
    }
}

namespace _impl {
    function one2many<T>(x: T | T[] | undefined): T[] {
        return x === undefined ? [] :
            Array.isArray(x) ? x : [x];
    }

    function isPromise(p: any): p is Promise<any> {
        return Boolean(p && typeof p.then === 'function');
    }

    function invoke(func: () => any) {
        let result = func();

        return isPromise(result) ?
            result : Promise.resolve(result);
    }

    export async function callMappedHandlers( state: string, 
                                        handlers: OneOrMany<MappedHandler>,
                                        arg: any): Promise<Str> {
        let mapped: string;

        handlers = one2many(handlers);

        for (let handler of handlers) {
            let ret = await invoke(() => handler(arg));
            if (typeof ret === 'string')
                mapped = ret;
        }

        return mapped;
    }

    export async function callTransitionHandlers( state: string, 
                                            name: string,
                                            handlers: OneOrMany<TransitionHandler>,
                                            arg: any): Promise<Str> {
        let toState: string;

        handlers = one2many(handlers);

        for (let handler of handlers) {
            let ret = await invoke(() => handler(arg));
            if (typeof ret === 'string')
                toState = ret;
        }

        return toState;
    }

    export async function callEnterHandlers(_my: Self, state: string, arg: any) {
        let handlers = one2many(_my.config[state].onEnter);

        for (let handler of handlers) {
            await invoke(() => handler(arg));
        }
    }

    export async function callLeaveHandlers(_my: Self, state: string) {
        let handlers = one2many(_my.config[state].onLeave);

        for (let handler of handlers) {
            await invoke(() => handler());
        }
    }
}
