import { toAscii } from "../core/AsciiMathUtils";
import { rand, shuffle } from "../core/Helpers";
import { formatters } from "./WProblem.Formatters";
import type { WProblemModel } from "./WProblem.Model";
import type { WProblemTemplate } from "./WProblem.Template";

type Text = WProblemModel["text"];
type Answers = WProblemModel["answers"];
type Variable = WProblemTemplate["variables"][0];


function applyFormat(value: string, fmt: string): string {
    var toMath = v => v.includes("/") ? toAscii(v) : v;

    if (value.includes("`"))
        return value;

    if (!fmt || fmt === "")
        return toMath(value);

    return formatters.apply(value, fmt);
}

function substitute(str: string, vars: Variable[], values: string[]) {
    let nodes = vars.map(($var, i) => {
        let value = applyFormat(values[i], $var.fmt);
        return {
            $var,
            uniqueName: `__${i}__`,
            value
        };
    });

    nodes.sort(
        (a, b) => b.$var.name.length - a.$var.name.length);

    nodes.forEach(node =>
        str = str.replaceAll(node.$var.name, node.uniqueName));

    nodes.forEach(node =>
        str = str.replaceAll(node.uniqueName, node.value));

    // do it again as formatters could introduce more vars

    nodes.forEach(node =>
        str = str.replaceAll(node.$var.name, node.uniqueName));

    nodes.forEach(node =>
        str = str.replaceAll(node.uniqueName, node.value));
    
    return str;
}

function makeText(template: WProblemTemplate, set: string[]): Text {
    return template.text.map(el => ({
        line: substitute(el.line, template.variables, set),
        css: el.css
    }));
}

function makeAnswers(template: WProblemTemplate, set: string[]): Answers {
    let answers: Answers = template.answers.map((ans, no) => ({
        name: template.answers[no],
        value: substitute(ans, template.variables, set),
        isRight: no == 0                // by design the first is the right
    }));

    return shuffle(answers);
}

let newWProblemLogic = (template: WProblemTemplate) => ({
    makeModel: (lastSetId = -1): WProblemModel => {
        let setId = rand(0, template.sets.length-1);

        while (setId === lastSetId && template.sets.length > 1)
            setId = rand(0, template.sets.length-1);

        let set = template.sets[setId];

        return {
            problemId: template.problemId,
            setId,
            text: makeText(template, set),
            answers: makeAnswers(template, set),
            options: template.options
        };
    }
});

export default newWProblemLogic;
export type WProblemLogic = ReturnType<typeof newWProblemLogic>;
