import { readable, writable } from "svelte/store";

import { Anki } from "../core/Anki"
import type { WProblemModel } from "./WProblem.Model";

let anki = Anki.getCard();

let newWProblemStore = (model: WProblemModel) => ({
    text: writable(model.text),
    problemId: readable(model.problemId),
    sets: readable(anki.sets),
    answers: model.answers.map(ans => ({
        value: writable(ans.value),
        isRight: writable(ans.isRight),
        isClicked: writable(false)
    }))
});

export default newWProblemStore;
export type WProblemStore = ReturnType<typeof newWProblemStore>;

