<script lang="ts">
    import { Confetti } from "svelte-confetti"

    import Ans from "./WProblem.Ans.svelte"
    import Grid from "../core/Grid.svelte";
    import Text from "./WProblem.Text.svelte"

    import newWProblemStore from "./WProblem.Store";
    import newWProblemTemplate from "./WProblem.Template";
    import newWProblemLogic from "./WProblem.Logic";
    import fsm from "./WProblem.Fsm"
    import { onMount } from "svelte";
    import { applyAsciiMath } from "../core/AsciiMathUtils";
    import { versions } from "../core/Version.Store";
    import Congrats from "../core/Congrats.svelte";
    import { formatters } from "./WProblem.Formatters";

    let template = newWProblemTemplate();
    console.log("template", template);

    let logic = newWProblemLogic(template);
    let model = logic.makeModel();
    console.log("model", model);

    let store = newWProblemStore(model);
//    console.log("store", store);

    let solved = false;
    let solvedRight = false;
    let congrats: Congrats;

    versions.add(template.problemId);

    onMount(() => {
        applyAsciiMath(1000);
    });

    function answerIsClicked(no: number) {
        if (solved) {
            makeNewProblem();
            return;
        }

        store.answers.forEach((ans, i) => 
            ans.isClicked.set(model.answers[i].isRight));

        store.answers[no].isClicked.set(true);

        solved = true;
        solvedRight = model.answers[no].isRight;

        if (solvedRight) congrats.play();
    }

    function makeNewProblem() {
        model = logic.makeModel(model.setId);

        store.text.set(model.text);
        store.answers.forEach((ans, i) => {
            ans.value.set(model.answers[i].value);
            ans.isRight.set(model.answers[i].isRight);
            ans.isClicked.set(false);
        });

        solved = false;
        solvedRight = false;

        applyAsciiMath(500);
    }

    function textIsClicked() {
        if (solved) {
            makeNewProblem();
        }
    }

    let layout = {
        areas: `
            "txt  txt  txt "
            "txt  txt  txt "
            "txt  txt  txt "
            "txt  txt  txt "
            "a0    .    a1 "
            "a2    .    a3 "
            ".     .    .  "
        `,
        cols: `2fr 1fr 2fr`,
        rows: `2fr 2fr 2fr 2fr 2fr 2fr 10px`
    };

</script>

<Grid on:click={() => textIsClicked()}
    {layout}
>
    <Congrats bind:this={congrats} />

    {#key model}
        <Text on:click={() => textIsClicked()}
            ga="txt" text={store.text} 
        />
    {/key}

    {#each store.answers as ans, no ({model}) }
        <Ans store = {ans}
            ga={`a${no}`} 
            on:click={() => answerIsClicked(no)}
        />
    {/each}
</Grid>

{#if model.options.hebTest }
    {#each formatters.tmHebrew as heb }
        <p class="text-xs">
            <bdi dir="rtl">
                {heb}
            </bdi>
        </p>
    {/each}
    -----------------------
    {#each formatters.tmTest as tst }
        <p class="text-xs">
            <bdi dir="rtl">
                {tst}
            </bdi>
        </p>
    {/each}
{/if}


