import { onMount } from "svelte";
import { readable } from "svelte/store";

function createVersions() {
    let _db: string[] = [];
    let _index: number = 0;
    let _set: (v: string) => void;

    let _current = readable<string>("", (set) => {
        _set = set; 
    });

    function update() {
        _set(_db.length > 0 ? _db[_index % _db.length] : "");
    }

	return {
        current: _current,
		add: (v: string, $default = false) => {
            onMount(() => {
                _db.push(v);

                if ($default)
                    _index = _db.length - 1;

                update();

                return () => {
                    _db = _db.filter(x => x !== v);
                    update();
                }
            });
        },
        next: () => {
            _index++;
            update();
        }
	};
}

export const versions = createVersions();