<script lang="ts">

    import {randFrom} from '../core/Helpers'
    import {pulse} from "./../core/Pulse.svelte"
    import {Mp3} from '../core/Mp3.svelte';
    import { getBgColor } from '../core/Grid.svelte';

    import {fire} from "./Model.svelte"

    export let ga: string;
    export let sz: string = "30px"

    let _go: string = "GO";
    let _bgColor = getBgColor(ga);

    function onClick() {
        fire("--evt-go");
        Mp3.play(randFrom(["yes", "no", "idle"]));
    }

</script>

<button
    class="{_bgColor}"
    style="grid-area:{ga};font-size:{sz}" 
    on:click={onClick}
    use:pulse
>
    {_go}
</button>
