import { Decimal } from "decimal.js"
import Fraction from "fraction.js"

import {error} from './Logger.svelte'

type Float = number | Decimal;
type OneOrMany<T> = T | T[];

export function throwError(msg: string): never {
    error(msg);
    throw(msg);
}

// WARN: rand(min, max) is inclusive   min <= result <= max
export function rand(min: number, max: number) {
    min = Math.ceil(min);
    max = Math.floor(max + 1);
    return Math.floor(Math.random() * (max - min) + min);
}

export function randFrom<T>(arr: readonly T[]): T {
    return arr[rand(0, arr.length-1)];
}

export function randFromIf<T>(arr: readonly T[], 
    iff: (x: T) => boolean): T 
{
    let filtered = arr.filter(iff);
    // assert there is any number in filtered array
    return filtered[rand(0, filtered.length-1)];
}

export const trueFalse = 
    () => rand(0, 1) == 0;

export const randSign = () => 
    rand(0, 1) == 0 ? +1 : -1;

export function pm(num: number): number
export function pm(num: Decimal): Decimal
export function pm(num: number | Decimal)
{
    let plusOrMinus = (rand(0, 1) == 0 ? +1 : -1);
    return typeof num === 'number' ?
        num * plusOrMinus : 
        num.mul(plusOrMinus);
}

export function randId(len: number, charSet?: string) {
    charSet ??= 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var str = '';
    for (var i = 0; i < len; i++) {
        var indx = Math.floor(Math.random() * charSet.length);
        str += charSet.charAt(indx);
    }
    return str;
}

export function shuffle<T>(src: readonly T[]): T[] {
    let dst = [...src];
    for (let i = dst.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [dst[i], dst[j]] = [dst[j], dst[i]];
    }
    return dst;
}

let audio = new AudioContext();

export function beep() {
    var osc = audio.createOscillator();
    var gain = audio.createGain();

    osc.connect(gain);
    gain.connect(audio.destination);

    gain.gain.value = 0.5;
    osc.frequency.value = 440;
    osc.type = "triangle";

    osc.start();

    setTimeout(() => osc.stop(), 50);
} 

export const run = (task: () => void) =>
    setTimeout(task, 0);

export const delay = (ms: number) =>
    new Promise(resolve => setTimeout(resolve, ms));

export const range = (start: number, end: number) =>
    Array.from({length: (end - start)}, (_, k) => k + start);

export function safe(func: () => void) {
    try {
        return func();
    }
    catch (err) {
        let name = func.name;
        if (name === "") name = "unknown";
        error(`Error in ${name}`, err);
    }
}

export const splitNoEmpty = (str: string, delim?: string) =>
    str.split(delim ?? " ").filter(Boolean).map(x => x.trim());

export const isFraction = (d: Decimal) =>
    !d.toFraction()[1].eq(1);

type MapT<T, Dst> = T extends any[] ? Dst[] : Dst;

export function n2d<T extends OneOrMany<Float>>(arg: T): MapT<T, Decimal> {
    if (typeof arg === 'number') 
        return <any> new Decimal(arg);

    if (!Array.isArray(arg))
        return <any> arg;

    return <any> arg.map(
        x => typeof x === 'number' ? new Decimal(x) : x);
}

export function d2n<T extends OneOrMany<Float>>(arg: T): MapT<T, number> {
    if (typeof arg === 'number') 
        return <any> arg;

    if (!Array.isArray(arg))
        return <any> arg.toNumber();

    return <any> arg.map(
        x => typeof x === 'number' ? x : x.toNumber());
}

export function d2t(d: Decimal, excludeWhole = true): string {
    let parts = d.toFraction();

    if (parts[1].eq(1))
        return d.toNumber().toLocaleString('en');

    let fr = new Fraction(d.toNumber());

    return fr.toFraction(excludeWhole);
}

export const nd2f = (n: Decimal, d: Decimal) =>
    new Fraction(n.toNumber(), d.toNumber());

export const d2f = (d: Decimal) => 
    new Fraction(d.toNumber());

export const f2d = (fr: Fraction) => 
    n2d(fr.n).div(fr.d);

export const t2d = (str: string) =>
    f2d(new Fraction(str));

export function allAreUnique<T>(arr: T[]): boolean {
    let set = new Set<string>();
    for (let x of arr) {
        let str = `${x}`;
        if (set.has(str)) return false;
        set.add(str);
    }
    return true;
}

export const vibrateSOS = () =>
    navigator.vibrate([100,30,100,30,100,30,200,30,200,30,200,30,100,30,100,30,100]);

export function isNumeric(value: string): boolean {
    return /^-?\d+$/.test(value);
}
