<script context="module" lang="ts">

    interface ILog {
        error(msg: string, err?: Error);
        info(msg: string, err?: Error);
    }

    let _lines: string[] = [];
    let _refresh = () => {};

    function log(level: string, msg: string, err: string) {
        let line = `${level} ${msg}`;
        if (err) line += ` (${err})`;

        console.log(line);

        _lines.push(line);
        _refresh();
    }


    export function info(msg: string, err?: Error) {
        log("I", msg, err?.message);
    }

    export function error(msg: string, err?: Error) {
        log("E", msg, err?.message);
    }


</script>


<script lang="ts">

    let lines: string[] = _lines;

    _refresh = () => {
        lines = _lines;
    }

</script>

{#each lines as line}
    <p>{line}</p>
{/each}
