import { text } from "svelte/internal";
import { Anki } from "../core/Anki"
import type { WProblemModel } from "./WProblem.Model";


type Text = WProblemModel["text"];
type Options = WProblemModel["options"];

function parseText(str: string): Text {
    str = str.replaceAll("\r", "\n");

    var lines = str
        .trim()
        .replace("U+", "&#x")
        .replaceAll("\r", "\n")
        .split("\n")
        .map(x => x.trim())
        .filter(x => x !== "");

    let text: Text = [];
    let css = "";

    for (var line of lines) {
        if (line.startsWith("{")) {
            if (!line.endsWith("}")) continue;
            css = line.slice(1, -1);
            continue;
        }
        text.push({line, css});
        css = "";
    }

    return text;
}

function parseValues(str: string): string[] {
    let tokens =
        str.includes('\t') ? str.split('\t') :
        str.includes('\n') ? str.split('\n') :
        str.includes(';') ? str.split(';') :
        str.includes(',') ? str.split(',') :
        str.split(' ');

    return tokens.map(s => s.trim());
}

function parseAnswers(str: string) {
    var answers = parseValues(str);
    return answers;
}

function parseVariables(str: string) {
    var values = parseValues(str);

    var vars = values.map(value => {
        var [name, fmt] = value.split(':');
        return {name, fmt: fmt ?? ""};
    });

    return vars;
}

function parseSets(str: string): string[][] {
    let lines =
        str.includes("<br>") ? str.split("<br>") :
        str.includes('\n') ? str.split('\n') :
        str.includes('\r') ? str.split('\r') :
        str.includes(';') ? str.split(';') :
        [str];

    lines = lines
        .map(ln => ln.trim())
        .filter(ln => ln != "");

    let sets = lines
        .map(ln => parseValues(ln));

    return sets;
}

function parseOptions(str: string): Options {
    var options = parseValues(str)
        .map(o => o.includes('=') ? o.split("=") : [o, ""]);

    let hebTest = false;

    options.forEach(o => {
        switch (o[0]) {
            case "heb-test": hebTest = true; break;
            default: console.log(`Unknown option '${o}'`); break;
        }
    });

    return {
        hebTest
    };
}

let valueOf = (str: string) => str.trim();

let anki = Anki.getCard();

let newWProblemTemplate = () => ({
    problemId: valueOf(anki.problemId),
    text: parseText(valueOf(anki.content)),
    answers: parseAnswers(valueOf(anki.answers)),
    variables: parseVariables(valueOf(anki.variables)),
    sets: parseSets(valueOf(anki.sets)),
    options: parseOptions(valueOf(anki.options))
});

export default newWProblemTemplate;
export type WProblemTemplate = ReturnType<typeof newWProblemTemplate>;